import { Component, OnInit } from '@angular/core';
import { RallyLead } from './rally-lead';

@Component({
  selector: 'app-rally-lead',
  templateUrl: './rally-lead.component.html',
  styleUrls: ['./rally-lead.component.css']
})
export class RallyLeadComponent implements OnInit {

  constructor() { }

  leads:RallyLead[] = [
    {
      name: "Beard",
      marchTime: 69,
      mark: <RallyLead>{}
    },
    {
      name: "Ant",
      marchTime: 420,
      mark: <RallyLead>{}
    }
  ]

  ngOnInit(): void {
  }

  add(a:RallyLead) {
    this.leads.push(a)
  }
  
}
